.App {
  padding: 5%;
  text-align: center;
  font-size: 16px;
}

.party-popper {
  width: 200px;
  height: 200px;
}

.fade {
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.age-stats {
  opacity: 1;
}

@-webkit-keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}


